import { Box } from "@mui/material"
import { SignUpInButton } from "../../sign-up-in/components/sign-up-in-button/SignUpIn"
import { AppBuildTime } from "./buildtime/AppBuildTime"
import Link from "next/link"

// import {LanguageSelect} from '../../../language-selector/LanguageSelect'

function Privacy(){
    return(
        <Box>
            <Link href="/privacies/zh-cn">隐私政策</Link>
        </Box>
    )
}
export const SettingsContent = () => {
    return (
        <Box>
            {/* <LanguageSelect /> */}
            <SignUpInButton back="/settings" />
            <AppBuildTime />
            <Privacy/>
        </Box>
    )
}